@import url(https://fonts.googleapis.com/css?family=Oswald:400|Sigmar+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Baloo+Tamma|Modak&display=swap);
@import url(https://fonts.googleapis.com/css?family=PT+Serif&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
	min-width: 400px;
}

ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}


body {
  margin: 0;
  font-family: 'PT Serif',serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  font-size: 20px;
  background-color: #f8fbfb;

}

.app {
  display: flex;
  justify-content: center;
  padding: 1rem;
  padding-top: 5rem;
}


.title {
  font-family: 'Baloo Tamma';
}


@media (max-width: 1425px) {
  html {
    font-size: 16px;
  }
}
@media (max-width: 1200px) {
  .app {
    padding-top: 6rem;
  }
}
@media (max-width: 600px) {
  html {
    font-size: 12px;
  }
}

.overlay:hover div.name {
  opacity: 0
}

.description {
  opacity: 0
}

.overlay:hover div.description {
  opacity: 1
}

.overlay {
  position: relative;
  max-width: 900px;
  margin-bottom: 1rem;
  cursor: help;
}

.overlay img {
  width: 100%;
  border-radius: 3px;
}


.name {
   width:100%;
   position: absolute;
   bottom: 0.1rem;
   left:0;
   display: flex;
   flex-direction: row;
   align-items: flex-end;
   justify-content: space-between;
   border-radius: 3px;
   background: rgba(50,50,50,0.5);
   color: white;  /*  alt-color: efefef */
}

.name h1 {
  font-size: 2rem;
  padding-left: 0.5rem;
}
.name h3 {
  padding: 0.5rem;
}

.name h4 {
  font-size: 1rem;
  padding: 0.6rem;
  padding-bottom: 0.25rem;
}

.description {
   width:100%;
   position: absolute;
   top:0;
   bottom: 0;
   left:0;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content:flex-start;
   background: rgba(0,0,0,0.8);
   color: white;
}

.description h1 {
  padding:1rem;
  width: 100%;
  font-size: 2rem;
  text-align: center;
}

.description p {
  font-size: 1.5rem;
  padding: 1rem;
  text-align: justify;
  text-justify: inter-word;
  text-indent: 1.5rem;
}

.menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 520px;
  background-color: #f8fbfb;
  padding: 0.5rem;
  margin-right: 2rem;
  margin-left: 1rem;
  padding-bottom: 1rem;
  box-shadow: 0px 3px 1px -2px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 1px 5px 0px rgba(0,0,0,0.12);
  border-radius: 4px;

}


.filters {
  width: 90%
}

.filters input {
  padding-top: 1.8rem;
  width: 100%;
  border: 0;
  border-bottom: 2px solid rgba(132,140,131,0.3);
  -webkit-transition: border-color 0.7s ease;
  transition: border-color 0.7s ease;
  box-shadow: none;
  background-color: #f8fbfb;
  outline: none;
  font-size: 1rem;
  color:#4E4F4D;
}

.filters .select {
    margin-top: 2rem;
}

.filters input:focus {
  border-color: rgba(68,132,206,1);

}

.filters p {
padding-top: 2rem;
font-size: 1.1rem;
color: #848c83;
}

.logo {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  margin-top: -80px;
  margin-bottom:  20px;
}

.logo h1 {
  color: #4484CE;
  font-size: 2.5rem;
}

.logo h1 span {
  color: #ff3b3f;
}

.logo img {
    width: 7rem;
    margin: 0 1rem -0.5rem 1rem;
}

.logo + p {
  font-size: 20;
  text-align: right;
  width: 100%;
  color: #929991;
  padding-right: 40px;
}

.arrow {
  display: none;
}

.menu-bar {
  display: none;

}

.rotateImage {
  -webkit-transform:rotate(180deg);
  transform: rotate(180deg);
}


@media (max-width: 1200px) {
  .menu {
    position: fixed;
    top: 0;
    left:0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 520px;
    background-color: #f8fbfb;
    padding-top: 6rem;
    margin-left: 0rem;
    z-index: 1000;

  }

  .arrow {
    display: block;
    width: 2rem;
    height: 2rem;
  }
  .close {
    position: absolute;
    top: 1rem;
    right: 1rem;
  }

  .menu-bar {
    position: fixed;
    top: 0;
    left:0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f8fbfb;
    padding: 1rem;
    z-index: 2000;
    border-radius: 3px;
    box-shadow: 0px 3px 1px -2px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 1px 5px 0px rgba(0,0,0,0.12);

  }

  .menu-bar h1 img {
      width: 3rem;
      padding: 0.3rem;
      margin: 0 1rem;
      padding-bottom: 0,
  }
  .menu-bar .title {
    font-size: 2.2rem;
    color: #4484CE;
    width: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: center;
  }
  .menu-bar .title span {
    color: #ff3b3f;
  }

  .hide {
    display: none;
  }
}


@media (max-width: 600px) {
  .menu {
    padding-top: 8rem;
  }
 }

